<template>
  <mrc_ess_myDialog :isopen='isopen'
                    @cancel='cancel'
                    @success='success'
                    :width='550'
                    title='查看下属人员数据'
                    :disabled='true'>
    <div style="overflow-x: hidden;">
      <mrc_ess_switch true-value="1"
                      false-value="0"
                      :readonly='readonly'
                      v-model="params.status"
                      :label="$t('启用')">
      </mrc_ess_switch>
      <mrc_ess_autoCompleteStaff v-model="params.staff_uuid"
                                 :label="$t('员工姓名')"
                                 :multiple='true'></mrc_ess_autoCompleteStaff>
      <mrc_ess_autoCompletePosition v-model="params.specified_post_uuids"
                                    :label="$t('被查看岗位')"
                                    :multiple='true'></mrc_ess_autoCompletePosition>
      <mrc_ess_autoCompleteStaff v-model="params.exclude_staff_ids"
                                 :label="$t('排除人员')"
                                 :multiple='true'></mrc_ess_autoCompleteStaff>
      <tree v-model="function_uuids"></tree>
    </div>
  </mrc_ess_myDialog>
</template> 

<script>
import { required } from 'vuelidate/lib/validators'
import tree from '../tree.vue'
export default {
  components: { tree },
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
    isopen: {
      type: Boolean,
      default: false
    },
    paramsData: {
      type: Object,
      default: () => { return {} }
    }
  },
  model: {
    prop: 'isopen',
    event: 'flag'
  },
  data () {
    return {
      params: {
        status: '1',
        post_uuid: '',
        high_grade_switch: '1',
        exclude_staff_ids: ''
      },
      type: 0// 0新增 1修改
    };
  },
  validations: {
    params: {
      post_uuid: {
        required
      }
    }
  },
  computed: {},
  created () { },
  watch: {
    paramsData (v) {
      if (Object.keys(v).length > 0) this.type = 1
      else this.type = 0
      this.params = this.$clone(v)
    }
  },
  mounted () {
    this.params = this.$clone(this.paramsData)
    if (Object.keys(this.params).length > 0) this.type = 1
    else this.type = 0
  },

  methods: {
    cancel () {
      this.$emit('flag', !this.isopen)
      this.$emit('cancel')
    },
    success () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      this.$emit('getData', this.params, this.type)
      this.$emit('flag', !this.isopen)
    },
  },
};
</script>

